import React from 'react';
import BeerCard from './BeerCard';

export default function FavoritesView(props) {
  const beerCards = props.beers.map((beer) => (
    <BeerCard
      key={beer.id}
      beer={beer}
      onHeartClick={(id) => props.onHeartClick(id)}
      onCardClick={(id) => props.onCardClick(id)}
    />
  ));
  return (
    <section className="section favorites">
      <header className="section__header">
        <h1 className="section__title">My Favorites</h1>
        {props.filterBar}
      </header>
      <div className="card-grid">{beerCards}</div>
    </section>
  );
}
